body {
  background: var(--system-background, #f6f8fa);
}

.container {
  width: 1440px;
  height: 1532px;
  background: var(--system-background, #f6f8fa);
  display: grid;
  grid-template-columns: 1fr 1fr 3fr 4fr;
  grid-template-rows: 0.2fr 1.5fr 1.2fr 0.2fr;
  grid-template-areas:
    "sidebar navbar navbar navbar "
    "sidebar main main main"
    "sidebar main main main"
    "sidebar main main main";
}

nav {
  grid-area: navbar;
}

main {
  grid-area: main;
}

#sidebar {
  grid-area: sidebar;
}

